import AuthConst from '../config/AuthConst';
import http from "./http";

const getByIdCliente = (idCliente) => {
  let url = AuthConst.BASE_URL + '/encargados/id-cliente/' + idCliente;
  return http.get(url)
};

const create = (encargado) => {
  let url = AuthConst.BASE_URL + '/encargados';
  return http.post(url, encargado)
};

const update = (encargado) => {
  let url = AuthConst.BASE_URL + '/encargados';
  return http.put(url, encargado)
};

const login = (encargado) => {
  let url = AuthConst.BASE_URL + '/encargados/login';
  return http.post(url, encargado)
};

export default { getByIdCliente, create, update, login }
