<template>
  <wrapper titulo="Encargados">
    <v-data-table :headers="headers" :items="encargados">
      <template v-slot:item.administrador="{ item }">
        <v-chip v-if="item.administrador" class="ma-2 font-weight-bold" color="primary" small>
          Administrador
        </v-chip>
        <v-chip v-else class="ma-2 font-weight-bold" small>
          Encargado
        </v-chip>
      </template>

      <template v-slot:item.cafeterias="{  }">
        <v-btn icon>
          <v-icon>
            mdi-eye
          </v-icon>
        </v-btn>
      </template>

      <template v-slot:item.acciones="{ item }">
        <v-btn icon @click="abrirParaEditar(item)">
          <v-icon>
            mdi-pencil
          </v-icon>
        </v-btn>
        <v-btn icon @click="abrirParaCambiarContrasena(item)">
          <v-icon>
            mdi-lock-reset
          </v-icon>
        </v-btn>
      </template>
    </v-data-table>

    <dialogo v-model="dialogOperarEncargado" :title="encargadoParaOperar.id ? 'Editar encargado' : 'Agregar encargado'">
      <v-form ref="formEncargado" lazy-validation>
        <v-row>
          <v-col cols="12" md="6">
            <v-text-field v-model="encargadoParaOperar.nombre" label="Nombre" :rules="textFieldRules" autofocus/>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="encargadoParaOperar.apellidoPaterno" label="Apellido Paterno" :rules="textFieldRules" />
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="encargadoParaOperar.apellidoMaterno" label="Apellido Materno"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="encargadoParaOperar.email" label="Correo" :rules="textFieldRules" />
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="encargadoParaOperar.telefono" label="Teléfono"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-checkbox v-model="encargadoParaOperar.administrador" label="Administrador"/>
          </v-col>
          <v-col v-if="encargadoParaOperar.id" cols="12" md="6">
            <v-checkbox v-model="encargadoParaOperar.activo" label="Activo"/>
          </v-col>
          <v-col cols="12">
            <h4>Seleccione las cafeterias para asignar</h4>
          </v-col>
          <v-col v-for="(cafeteria, indexCafeteria) in cafeterias" :key="indexCafeteria" cols="12" md="6">
            <v-checkbox :label="cafeteria.nombre"/>
          </v-col>
        </v-row>
      </v-form>

      <template slot="actions">
        <v-btn text color="primary" plain @click="encargadoParaOperar.id ? editarEncargado() : guardarEncargado()">Guardar</v-btn>
      </template>
    </dialogo>

    <dialogo v-model="dialogCambioContrasena" :title="encargadoParaOperar && encargadoParaOperar.id ? 'Cambiar contraseña para ' + encargadoParaOperar.nombre +
     ' ' + encargadoParaOperar.apellidoPaterno : ''">
      <v-form v-model="formEncargado" ref="formContrasena" lazy-validation>
      <v-row>
        <v-col cols="12">
          <v-text-field v-model="pass" label="Contraseña" :rules="textFieldRules" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'" @click:append="show1 = !show1"/>
        </v-col>
        <v-col cols="12">
          <v-text-field v-model="passConfirm" label="Repetir contraseña" :rules="textFieldRulesPass"
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'" :type="show2 ? 'text' : 'password'"  @click:append="show2 = !show2"/>
        </v-col>
      </v-row>
      </v-form>
      <template slot="actions">
        <v-btn text color="primary" plain @click="cambiarContrasena">Guardar</v-btn>
      </template>
    </dialogo>

    <v-btn fixed color="primary" bottom right fab @click="abrirParaOperarEncargado({})">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </wrapper>
</template>

<script>
    import CafeteriasService from "../../services/CafeteriasService";
    import EncargadosService from "../../services/EncargadosService";
    import UsuariosService from "@/services/UsuariosService";

    export default {
        name: "Usuarios",
      data(){
          return{
            dialogOperarEncargado: false,
            encargados: [],
            cafeterias: [],
            encargadoParaOperar: {},
            headers: [
              { text: 'Nombre', value: 'nombreCompleto' },
              { text: 'Correo', value: 'email' },
              { text: 'Teléfono', value: 'telefono' },
              { text: '', value: 'administrador' },
              { text: 'Cafeterias', value: 'cafeterias' },
              { text: '', value: 'acciones' }
            ],
            textFieldRules: [v => !!v || 'Este campo es requerido'],
            textFieldRulesPass: [v => v === this.pass || 'Las contraseñas no coinciden'],
            dialogCambioContrasena: false,
            show1: false,
            pass: null,
            passConfirm: null,
            show2: false,
            formEncargado: false
          }
      },
      async created() {
        this.$loader = true

        try {
          this.encargados = await EncargadosService.getByIdCliente(this.$usuario.idCliente);
          this.cafeterias = await CafeteriasService.getCafeterias(this.$usuario.idCliente);
        } catch (e) {
          this.encargados = []
          this.cafeterias = []
        }
        this.$loader = false
      },
      watch: {
        dialogCambioContrasena(val) {
        if (val){
          this.$nextTick(() => (this.$refs.formContrasena.reset()));
        }
        }
      },
      methods: {
        abrirParaOperarEncargado (encargado) {
          this.encargadoParaOperar = JSON.parse(JSON.stringify(encargado))
          this.dialogOperarEncargado = true
          this.$nextTick(() => (this.$refs.formEncargado.resetValidation()));
        },
        guardarEncargado () {
          if (this.$refs.formEncargado.validate()) {
            const vm = this
            vm.$loader = true
            const savePromise = vm.encargadoParaOperar.id ? EncargadosService.update : EncargadosService.create;
            vm.encargadoParaOperar.cliente = vm.$usuario.cliente
            vm.encargadoParaOperar.idCliente = vm.$usuario.cliente.id
            console.log(vm.encargadoParaOperar)
            savePromise(vm.encargadoParaOperar)
              .then(encargadoGuardado => {
                console.log('guardado', encargadoGuardado)
                if (!vm.encargadoParaOperar.id)
                  vm.encargados.push(encargadoGuardado);
                else {
                  const index = vm.$getIndex(vm.encargados, 'id', vm.encargadoParaOperar)
                  vm.$set(vm.encargados, index, encargadoGuardado)
                }

                vm.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Usuario guardado.', true);
              })
              .catch(() => {
                vm.$showAlerta('600', 'error', 'mdi-alert-circle',
                  'No fue posible guardar el usuario.', true);
              })
              .finally(() => {
                vm.dialogOperarEncargado = false
                vm.$loader = false
              })
          }
        },
        abrirParaEditar(encargado){
          this.encargadoParaOperar = encargado
          this.dialogOperarEncargado = true
        },
        editarEncargado(){
          if (this.$refs.formEncargado.validate()) {
            const vm = this
            vm.$loader = true
            const savePromise = EncargadosService.update
            vm.encargadoParaOperar.cliente = vm.$usuario.cliente
            vm.encargadoParaOperar.idCliente = vm.$usuario.cliente.id
            savePromise(vm.encargadoParaOperar)
              .then(encargadoEditado => {
                  const index = vm.$getIndex(vm.encargados, 'id', vm.encargadoParaOperar)
                  vm.$set(vm.encargados, index, encargadoGuardado)
                vm.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Encargado editado.', true);
              })
              .catch(() => {
                vm.$showAlerta('600', 'error', 'mdi-alert-circle',
                  'No fue posible editar el encargado.', true);
              })
              .finally(() => {
                vm.dialogOperarEncargado = false
                vm.$loader = false
              })
          }
        },
        abrirParaCambiarContrasena(encargado){
          this.encargadoParaOperar = encargado
          this.dialogCambioContrasena = true
        },
        async cambiarContrasena(){
          if (this.$refs.formContrasena.validate()) {
            this.$loader = true
            try {
              await UsuariosService.cambiarContrasena(this.encargadoParaOperar.id, this.passConfirm)
              this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Contraseña guardada exitosamente.', true);
            }catch (e){
              this.$showAlerta('600', 'error', 'mdi-alert-circle',
                'No fue posible guardar contraseña.', true);            }
          }
          this.$loader = false
          this.dialogCambioContrasena = false
        }
      }
    }

</script>

<style scoped>

</style>
